.Header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 70px;
  display: flex;
  background-color: #F5F7F8;
  justify-content: center;
  flex-direction: row;
  z-index: 100000;
}

.logo {
  display: flex;
  height: 70px;
}

.Container-Header {
  display: flex;
  flex-direction: row; /* Asegura que el contenedor esté alineado correctamente */
}

.Container-Header button {
  width: 100px;
  height: 60px;
  background: none;
  border: none;
  color: #1F1F1F;
  display: block; /* Cambia a block para asegurarse de que el botón ocupe toda el área */
  cursor: pointer;
  position: relative;
  font-size: 16px;
  transition: transform 0.5s ease;
}

.Container-Header button span {
  display: block;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center; /* Centra horizontalmente */
  align-items: center; /* Centra verticalmente */
}

.Container-Header button:hover {
  transform: scale(1.1);
}

.Container-Header button::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -10px;
  width: 0;
  height: 4px;
  background-color: #F4CE14;
  transition: width 0.5s ease;
}

.Container-Header button:hover::after {
  width: 100%;
}

.Linkto {
  text-decoration: none;
  color: inherit;
}
