.Container-TopSelectorbySides{
    display: grid;
    overflow: hidden;

}
.Container-TopSelectorbySides h2{
    font-size: 26px;
    font-weight: bold;
    color: #1F1F1F;
    text-align: center;
}


.Container-TSSTopSide{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    width: 100%;
    height: 12vh;
    justify-content: center;
}
.TopSideButton{
    width: 100%;
    height: 100%;
    border: 0;
    background-color: #F5F7F8;
    color: #1F1F1F;
    border-bottom-width: 10px;
    border-bottom-style: solid;
    border-bottom-color: #2E2E2E;
    position: relative;
    z-index: 1;
}

.TopSideButton h3{
    font-size: 20px;
    transition: font-size 0.5s ease-in-out;
    color: #2E2E2E;
}

.TopSideButton:hover{
    
    border-bottom-color: #F4CE14;
    cursor: pointer;
}

.TopSideButton:hover h3{
    font-size: 24px;
}

.Container-TSSBotSide{
    background-color: #1F1F1F;
    z-index: 0;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: auto auto;
    justify-content: start;
    align-items: center;
}

.Img-TSbS{
    height: auto;
    max-width: 864px;
}
.TSSBotSide-text{
    width: 80%;
    height: 100%;
    color: #F5F7F8;
    margin-left: 100px;
    margin-top: 30px;

}
.TopSideButton.active{
    border-bottom-color: #F4CE14;
}