.Container-TSC{
    display: flex;
    flex-direction: column ;
    flex-wrap: wrap;
    position: relative;
    grid-row-gap:10px;
    justify-content: center; /* Centra las imágenes horizontalmente */
    align-items: center; /* Centra las imágenes verticalmente */
    justify-items: center;
    margin: 20px;
}
.Container-TSC h1{
    color: #1F1F1F;

}
.Container-FS{
    justify-items: start;
}
.Container-SS{
    justify-items: end;
}
.Container-FS, .Container-SS{
    display:grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
    justify-content: space-between;
    align-items: stretch;

}

.FS-img, .SS-img{
    width: 33vw;

}
.FS-img{
    align-self: end;

}
.SS-img{
    align-items: start;
}
.FS-text, .SS-text{
    display: grid;
    align-items: center;
    align-content: center;
    width: 33vw;
}

.FS-text{

    text-align: left;
}

.SS-text{
    text-align: left;
}
.FS-text p, .FS-text h2{
    color: #1F1F1F;
}
.SS-text p, .SS-text h2{
    color: #1F1F1F;
}