.login-Container{
width: 98.9vw;
height: 100vh;
background-color: #2E2E2E;
overflow:hidden;
}

.login-Title-Wrapper h2{
    font-size: 2em;
    font-weight: bold;
    color: #F5F7F8;
    text-align: center;
  
}


.Main-Login-Container{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 400px;
    background-color: white;
    border-radius: 10px;
    padding-bottom: 20px;  
}

.Main-Login-Container form{
    padding: 0 40px;
    box-sizing: border-box;
}
.Main-Login-Container h2{
    text-align: center;
    padding: 0 0 20px 0;
    border-bottom: 1px solid #495E57;
}
form .username, form .password, form .email, form .telefono, form .nombre{
    position:relative;
    border-bottom: 2px solid black;
    margin: 30px 0;
}
.username input, .password input, .email input, .telefono input, .nombre input{
    width: 100%;
    padding: 0 5px;
    height: 40px;
    font-size: 16px;
    border: none;
    background: none;
    outline: none;
}

.username label,.password label, .email label, .telefono label, .nombre label{
    position: absolute;
    top: 50%;
    left: 5px;
    transform: translateY(-50%);
    font-size: 16px;
    pointer-events: none;
}

.username span::before, .password span::before, .email span::before, .nombre span::before, .telefono span::before{
    content:'';
    position: absolute;
    top: 40px;
    left: 0;
    width: 100%;
    height: 2px;
    background: #F4CE14;
}

.username input:focus ~ label,
.username input:valid ~ label{

    top: -5px;

    color: #F4CE14;

    transition: top 0.3s ease, color 0.3s ease;

}
.username input:focus ~ span::before,
.username input:focus ~ span::before{
    width: 100%;
}

.password input:focus ~ label,
.password input:valid ~ label{

    top: -5px;

    color: #F4CE14;

    transition: top 0.3s ease, color 0.3s ease;

}
.password input:focus ~ span::before,
.password input:focus ~ span::before{
    width: 100%;
}

.email input:focus ~ label,
.email input:valid ~ label{

    top: -5px;

    color: #F4CE14;

    transition: top 0.3s ease, color 0.3s ease;

}
.email input:focus ~ span::before,
.email input:focus ~ span::before{
    width: 100%;
}

.telefono input:focus ~ label,
.telefono input:valid ~ label{

    top: -5px;

    color: #F4CE14;

    transition: top 0.3s ease, color 0.3s ease;

}
.telefono input:focus ~ span::before,
.telefono input:focus ~ span::before{
    width: 100%;
}

.nombre input:focus ~ label,
.nombre input:valid ~ label{

    top: -5px;

    color: #F4CE14;

    transition: top 0.3s ease, color 0.3s ease;

}
.nombre input:focus ~ span::before,
.nombre input:focus ~ span::before{
    width: 100%;
}


.forgot{
    margin: -5px 0 20px 0;
    color: blue;
    cursor: pointer;
}

.recordar:hover{
    text-decoration: underline;
}

.submit-login{
    width: 100%;
    height: 50px;
    border: 1px solid;
    background-color: #F4CE14;
    border-radius: 25px;
    font-size: 16px;
    color: #2E2E2E;
    cursor: pointer;
    outline: none;
}