.Container-ContentBoxFourSides{
    text-align: left;
    
}
.Container-ContentBoxFourSides h2{
    font-size: 2em; /* O el tamaño que prefieras */
    font-weight: bold; /* Para mantener el peso de un h1 */
    color: #1F1F1F;
}
.Container-BFS-Wrapper{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    overflow: hidden;
}

.Container-BFS-A{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));    
    max-width: 100vw;    
    max-height: 65vh;
}

.Container-BFSItemBox{
    display: grid;
    max-height: 65vh;
    min-height: 30vh;
    height: 65vh;
    position: relative;
    transition: all 0.3s ease-in-out;
    text-align: center;
    transition: all 0.5 ease;
}

.Container-BFSItemBox.clicked:hover{
    cursor: pointer;
    transform: scale(1.2);
    z-index: 3;
}

.Container-BFSItemBox.clicked.active:hover{
    cursor: pointer;
    transform: scale(1);
    z-index: 3;
}

.Container-BFSItemBox:hover::before{
    content: '';
    position: absolute;
    bottom: 0;
    right: 0; /* Coloca el degradado a la derecha */
    width: 100%; 
    height: 50%;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.8), transparent); /* Degradado oscuro a transparente */
    transition: all 0.5 ease;
}


.Container-BFSItemBox h3{
    margin: 0%;
    position: absolute;
    margin-left: 15%;
    margin-right: 15%;
    bottom: 10%;
    opacity: 0%;
    transition: opacity 0.5 ease;
    width: 70%;
    color: #F5F7F8
}

.Container-BFSItemBox:hover h3{
    opacity: 1;
}


  .Container-BFSItemBox.clicked:hover::before{
    content: '';
    position: absolute;
    bottom: 0;
    right: 0; /* Coloca el degradado a la derecha */
    width: 100%; 
    height: 50%;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.8), transparent); /* Degradado oscuro a transparente */
    transition: all 0.5 ease;
}

.Container-BFSItemBox.hidden {
    display: none;
    opacity: 0; /* Ocultar elementos con transición */
    pointer-events: none; /* Evitar interacción con elementos ocultos */
  }

.BFSimg{
    display: flex;
    width: 100%;
    height: 100%;
    object-fit: cover; /* La imagen llenará el contenedor sin deformarse */
    object-position: left; /* Alinea la imagen a la izquierda */
}

.Container-BFS-B {
    display: grid;
    margin: 0px 50px;
    opacity: 1;
    transition: opacity 0.3s;
  }
  .buttonwrapper{
    display: grid;
    justify-items: end;
  }

  .vermasbtn{
    justify-items: end;
    font-size: 16px;
    background-color: #F5F7F8;
    color: #1F1F1F;
    border-right: none;
    border-left: none;
    border-top: none;
    border-bottom: 0;
    border-bottom-width: 10px;  
    border-color: #F4CE14;
    transition: scale 0.3s ease;
  }


  .vermasbtn:Hover{
   cursor: pointer; 
    scale: 1.1;
    border-bottom-width: 10px;
  }