/* Ajusta el ancho del contenedor y el margen */
.Container-SelectCB {
    display: grid;
    width: 100%; 
    max-height: 520px;

}

.Container-SCB{
    display: grid;
    grid-template-columns: 1fr 3fr;
    justify-content: space-between;
}

.Container-SCB-A{
    max-height: 80%;
    direction: rtl;
    display:grid;
    grid-template-rows: repeat(25%);
    overflow-y: auto;
}

.Container-SCB-A * {
    direction: ltr; /* Mantener el contenido en la dirección natural de izquierda a derecha */
}
/* Estilos para el contenedor flex en columna */
.Container-SCB-A button {
    border-left: 10px solid #F4CE14; /* Borde solo en el lado izquierdo */
    border-top: none; /* Sin borde en la parte superior */
    border-right: none; /* Sin borde en el lado derecho */
    border-bottom: none; /* Sin borde en la parte inferior */
    background-color: #F5F7F8; /* Fondo transparente */
    color: #495E57; /* Color del texto en negro */
    padding: 10px 20px; /* Espaciado interno */
    font-size: 16px; /* Tamaño de la fuente */
    cursor: pointer; /* Cambia el cursor al pasar sobre el botón */
    transition: all 0.3s ease; /* Transición suave */
    outline: none; /* Sin borde al hacer clic */
    text-transform: uppercase;
}

.Container-SCB-A button:hover,
.Container-SCB-A button.active { /* El color del hover permanece cuando está activo */
    border-color: #2E2E2E;
    color: #2E2E2E; /* Color del texto en negro */  
    background-color: #F4CE14;
}
/* Estilo para los encabezados dentro de Container-SCB-A */
.Container-SCB-A h3 {
    text-align: left;
}

/* Estilo para centrar el contenido y ocultar el desbordamiento */
.Container-SCB-B {
    z-index: 0;
    display: flex;
    align-items: center; /* Centra verticalmente */
    justify-content: center; /* Centra horizontalmente */
    overflow: hidden; /* Oculta cualquier contenido que sobresalga del contenedor */
    height: 80%; /* Ocupa toda la altura del contenedor padre */
    width: 100%; /* Ocupa todo el ancho del contenedor padre */
    position: relative;
}

/* Estilo para la imagen dentro de Container-SCB-B */
.Container-SCB-B .SCB-img {
    width: 100%; /* Ocupa todo el ancho del contenedor */
    max-height: 100%; /* Ocupa toda la altura del contenedor */
    object-fit: fill; /* Estira o comprime la imagen para llenar el contenedor */
    position: relative;
}
.Container-SCB-C{

}
.Container-SCB-C h3{
    text-transform: uppercase;
    text-align: center;
    color: #F5F7F8;
}
.SelectCB-AllContainer{
    background-color: #2E2E2E;
}
.Container-SCB-Bot-B{
    padding-bottom: 20px;
    margin-bottom:15px;
    display: grid;
    grid-template-columns: 1fr 3fr;

}
.Container-SCB-Bot-B p{
    grid-column: 2 / 3;
    padding-top: 7px;
    padding-right: 50px;
    color: #F5F7F8;
}
