.TablaEras-Container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%; /* Ajustar a 100% */
    overflow: hidden; /* Permitir desplazamiento horizontal */
}

.content-table {
    border-collapse: collapse;
    margin: 25px 0;
    border-radius: 5px 5px 0 0;
    min-width: 400px;
    width: 90%; /* Ancho total */
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

.content-table thead tr {
    background-color: #2E2E2E;
    color: #F5F7F8;
    text-align: center;
    font-weight: bold;
}
.content-table tbody tr:hover {
    scale: 1.01;
}

.content-table th,
.content-table td {
    padding: 12px 15px;
    text-align: center; /* Centramos el contenido */
    
}

.content-table tbody tr {
    transition: scale 0.3s ease-in-out;
    border-bottom: 1px solid #495E57;
    background-color: #F5F7F8;
}

.content-table tbody tr:nth-of-type(even) {
    background-color: #FAE48C;
}

.content-table tbody tr:last-child {
    border-bottom: 2px solid #2E2E2E;
}

.DownloadButton {
    transition: transform 0.3s ease-in-out; /* Transición para el efecto hover */
    background: none; /* Sin fondo */
    border: none; /* Sin borde */
    padding: 0; /* Sin padding */
    cursor: pointer; /* Cambiar cursor al pasar el ratón */
    display: flex; /* Asegura que el contenido se centre dentro del botón */
    justify-content: center;
    align-items: center;
  }
  
  .DownloadButton img {
    filter: invert(43%) sepia(8%) saturate(0%) hue-rotate(0deg) brightness(60%) contrast(120%);
    max-width: 40px; /* Ancho máximo de 50 píxeles */
    max-height: 40px; /* Alto máximo de 50 píxeles */
    display: block; /* Elimina el espacio adicional debajo de la imagen */
    transition: filter 0.3s ease-in-out, transform 0.3s ease-in-out; /* Transición de filtro y tamaño */
  }
  
  .DownloadButton:hover img {
    filter: none; /* Restaura el filtro al pasar el ratón */
    transform: scale(1.1); /* Aplica un ligero zoom al pasar el ratón */
  }  

  .Sort{
    background: none; /* Sin fondo */
    border: none; /* Sin borde */
    padding: 0; /* Sin padding */
    cursor: pointer; /* Cambiar cursor al pasar el ratón */
    align-items: center;
  }
  .Sort img {
    filter: invert(99%) sepia(0%) saturate(0%) brightness(1000%) contrast(100%);
    max-width: 25px; /* Ancho máximo de 50 píxeles */
    max-height: 25px; /* Alto máximo de 50 píxeles */
    display: block; /* Para evitar el espacio adicional debajo de la imagen */
    transition: filter 0.3s ease-in-out;
    transition: scale 0.3s ease-in-out;
  }
  .Sort img:hover{
    scale: 1.1;
    filter:none;
  }

/* Media Query para Pantallas Pequeñas (Móviles) */
@media (max-width: 600px) {
    .content-table {
        width: 100%; /* Ajustar ancho a 100% */
        overflow-x: auto; /* Habilitar desplazamiento horizontal */
    }

    .content-table, .content-table thead, .content-table tbody, .content-table th, .content-table td, .content-table tr {
        display: block; /* Convertimos todo en bloques */
    }
    
    .content-table thead tr {
        display: none; /* Ocultamos los encabezados en móviles */
    }
    
    .content-table tbody tr {
        margin-bottom: 15px; /* Espaciado entre filas */
        border-bottom: 1px solid #ddd;
    }

    .content-table tbody tr td {
        display: flex;
        justify-content: space-between;
        padding: 10px;
        text-align: left;
        font-size: 14px; /* Ajustamos el tamaño de fuente para móviles */
        position: relative;
        padding-left: 50%; /* Para dejar espacio para el pseudo-elemento */
    }
    
    
    .content-table tbody tr td:before {
        content: attr(data-label); /* Mostrar el encabezado correspondiente */
        position: absolute;
        left: 10px;
        font-weight: bold;
    }
}

