.Container-Footer{
    display: grid;
    grid-template-rows: auto auto;
    width: 100%;
    height: 100%;
    padding-top: 25px;
    background-color: #2E2E2E;

}
.Container-Footer p {
    color: #F5F7F8; /* Color de texto en el footer */
}


.Footer-text ul p {
    margin-top: 5px;
    margin-bottom: 10px; /* Márgenes para todos los <p> dentro de <ul> */
}

.Container-Footer-A{

    display: grid;
    grid-template-rows: auto auto;
    margin-top: 30px;
    margin-bottom: 30px;
    margin-left: 125px;
    margin-right: 125px;

}
.Container-Footer-Text{
    display: grid;
    grid-template-columns: 33% 33% 33%;
    list-style-type: none;
}
.Footer-text-B p{
    margin-left: 125px;
    margin-right: 125px;
    color: #2E2E2E;
    margin-bottom: 20px;

}

.Wrap-Footer .Footer-Logo{
    height: 70px;
}
.Container-Footer-B{
    width: 100%;
    height:fit-content;
    background-color: #F5F7F8;
}
.Footer-text h3{
    color: #F5F7F8;
    text-align: left;
}

.F-izq{
    font-weight: bold;  
    font-size: large;
}