.container-ContainerSlider{
    width: 100%;
    height: 100%;
    display: grid;
}

.title-CS-wrapper{
    text-align: center;
}
.title-CS-wrapper h2{
    font-size: 2em;
    font-weight: bold;
    color: #1F1F1F;
    text-align: center;
}

.CS-SliderContainer{
    margin-left: 100px;
    margin-right: 100px;
    display: grid;
    align-items: center;
}

.CS-Slider{
    display: grid;
    position: relative;
    grid-template-columns: auto 40px;
    width: 100%;
    height: fit-content;
    border-top: 0;
    border-left: 0;
    border-right: 0;
    border-bottom: 1px solid #45474B;
    margin-bottom: 15px;

}

.CS-Slider:hover{
    border-bottom: 5px solid #F4CE14;
    cursor: pointer;
}


.CS-img{
    max-width: 860px;
    height: auto;
}

.CS-iconbutton{
    height: 30px;
    width: 30px;
    cursor:pointer;
}

.CS-BotContainer{
    display:  grid;
    grid-template-columns: 2fr auto;
    width: 100%;
    height: 100%;
    margin-bottom: 15px;
    justify-content: center;
}
.CS-BotContainer p{
    margin-right: 20px;
}