.AdminDashboard-Container {
    display: flex; /* Flexbox para centrar */
    flex-direction: column; /* Elementos en columna */
    justify-content: center; /* Centrado vertical */
    align-items: center; /* Centrado horizontal */
    width: 98.9vw; /* Ocupa todo el ancho */
    height: 90vh; /* Ocupa toda la altura */
    background-color: #1F1F1F; /* Fondo rojo */
}

.AD-Wrapper {
    display: flex; /* Flexbox para organizar los elementos */
    flex-direction: row; /* Elementos en columna */
    align-items: center; /* Centrado horizontal */
    gap: 40px; /* Espaciado entre los elementos */
    width: fit-content; /* Ajusta el ancho al contenido */
}

.AdminDashboard-Container h2 {
    font-size: 3em; /* Tamaño más grande para el texto */
    font-weight: bold; /* Negrita */
    color: #F5F7F8; /* Texto negro */
    text-align: center; /* Centrado */
    margin: 0; /* Sin márgenes */
    margin-bottom: 50px; /* Espacio adicional entre el h2 y los botones */
}

.AD-btn {
    width: 350px; /* Ancho fijo para los botones */
    height: 150px; /* Altura fija para los botones */
    background-color: #B8950D; /* Fondo negro */
    color: #FFF; /* Texto blanco */
    font-size: 1.5em; /* Tamaño del texto */
    font-weight: bold; /* Texto en negrita */
    text-align: center; /* Centrado del texto */
    display: flex; /* Flexbox para el contenido del botón */
    justify-content: center; /* Centrado horizontal */
    align-items: center; /* Centrado vertical */
    border: none; /* Sin bordes */
    border-radius: 10px; /* Bordes redondeados */
    cursor: pointer; /* Cursor de clic */
    text-decoration: none; /* Sin subrayado en los enlaces */
    transition: transform 0.2s ease, background-color 0.3s ease; /* Transición en hover */
}

.AD-btn:hover {
    background-color: #F4CE14; /* Fondo más claro al pasar el mouse */
    transform: scale(1.1); /* Zoom más grande al pasar */
}

.Linkto {
    text-decoration: none; /* Sin subrayado */
    color: #1F1F1F; /* Texto blanco */
    text-align: center; /* Centrado */
    display: flex; /* Hacer que el enlace sea un contenedor flexible */
    justify-content: center; /* Centrar el texto horizontalmente */
    align-items: center; /* Centrar el texto verticalmente */
    width: 100%; /* Ocupa todo el ancho del botón */
    height: 100%; /* Ocupa toda la altura del botón */
}
